.input_div {
  float: left;
  width: 27.33%;
  margin: 40px 3%;
  position: relative;
} /* necessary to give position: relative to parent. */
.play_input:focus {
  outline: none;
}
.play_input{
  font: 15px/24px 'Rajdhani', sans-serif;
  color: white;
  width: 100%;
  box-sizing: border-box;
  letter-spacing: 1px;
  padding:1%;
  border: 0;
  padding: 4px 0;
  border-bottom: 1px solid #ccc;
  background-color: transparent;
}
.play_input ~ .focus-border {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  z-index: 99;
}
.play_input ~ .focus-border:before,
.play_input ~ .focus-border:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 100%;
  background-color: rgba(118, 223, 255, 0.877);
  transition: 0.4s;
}
.play_input ~ .focus-border:after {
  left: auto;
  right: 0;
}
.play_input:focus ~ .focus-border:before,
.play_input:focus ~ .focus-border:after,
.has-content.play_input ~ .focus-border:before,
.has-content.play_input ~ .focus-border:after {
  width: 50%;
  transition: 0.4s;
}
.play_input ~ label {
  position: absolute;
  left: 0;
  width: 100%;
  top: 9px;
  color: #aaa;
  transition: 0.3s;
  z-index: -1;
  letter-spacing: 0.5px;
}
.play_input:focus ~ label,
.has-content.play_input ~ label {
  top: -16px;
  font-size: 12px;
  color: rgba(118, 223, 255, 0.877);
  transition: 0.3s;
}
